/* Main container for the form */
.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height:100%;
    /* Set the container to take up the entire viewport height */
  }
  
  /* Style for the form */
  form {
    display: flex;
    flex-direction: column;
    width: 500px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  /* Style for labels */
  form label {
    display: grid;
    grid-template-columns: 1fr auto;
    margin-bottom: 1px;
    font-weight: bold;
  }
  
  /* Style for input fields */
  .input-field {
    padding: 9px;
    border: 1px solid #ccc;
    border-radius: 3px;
  }
  
  /* Style for the submit button */
  button[type="submit"] {
    background-color: #4CAF50; /* Green */
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    align-self: flex-end; /* Align the button to the end of the flex container */
  }
  